<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">Suppliers</div>

          <b-list-group>
            <b-list-group-item v-for="item, index in suppliers" :key="index">
              <template v-if="!item.isEditing">
                <b-row>
                  <b-col>
                    {{item.code}} - {{item.name}}
                    <span class="pull-right">
                      <b-link @click="editSupplier(item, index)">Edit</b-link>
                      <b-link class="ml-1 mr-1">|</b-link>
                      <b-link @click="removeSupplier(item, index)">Remove</b-link>
                    </span>
                  </b-col>
                </b-row>
              </template>
              <template v-if="item.isEditing">
                <b-row>
                  <b-col>
                    <b-form-input type="text" placeholder="Enter code" v-model="item.code"></b-form-input>
                  </b-col>
                  <b-col>
                    <b-form-input type="text" placeholder="Enter name" v-model="item.name"></b-form-input>
                  </b-col>
                  <b-col style="padding-top: 10px">
                    <span class="pull-right">
                      <b-link @click="saveSupplier(item, index)">Save</b-link>
                      <b-link class="mr-1 ml-1">|</b-link>
                      <b-link @click="cancelEdit(item, index)">Cancel</b-link>
                    </span>
                  </b-col>
                </b-row>
              </template>
            </b-list-group-item>
          </b-list-group>

          <b-button style="margin-top: 10px" variant="success" @click="addSupplier">Add Supplier</b-button>
        </b-card>
      </b-col>
      <b-col></b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: "Settings",
  components: {},
  data() {
    return {
      price: "",
      weight: "",
      suppliers: []
    };
  },
  mounted: function() {
    this.appSettings.suppliers &&
      this.appSettings.suppliers.forEach(item => this.suppliers.push(item));
  },
  computed: {},
  methods: {
    removeSupplier: function(item, index) {
      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to delete this supplier?`)
        .then(async value => {
          if (value) {
            this.suppliers.splice(index, 1);
            this.appSettings.suppliers = this.suppliers;
            this.saveSettings();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    saveSettings: async function() {
      await this.$fbServices.settingService.setSettings(this.appSettings);
      this.showMessage("Settings updated!");
    },
    addSupplier: function() {
      this.suppliers.push({
        name: "",
        code: "",
        isEditing: true
      });
    },
    editSupplier: function(item, index) {
      item.isEditing = true;
      this.suppliers.splice(index, 1, item);
    },
    cancelEdit: function(item, index) {
      delete item.isEditing;
      this.suppliers.splice(index, 1, item);
    },
    saveSupplier: function(item, index) {
      delete item.isEditing;
      this.suppliers.splice(index, 1, item);
      this.appSettings.suppliers = this.suppliers;
      this.saveSettings();
    }
  }
};
</script>
