<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <i class="cui-user icons font-lg"></i>
            Orders ({{orderCount}})
            <b-link class="pull-right" @click="addNewOrder">Add New Order</b-link>
          </div>
          <b-table
            responsive="sm"
            :items="orders"
            :fields="fields"
            :small="true"
            selectable
            select-mode="single"
            @row-clicked="orderClicked"
            :busy="isLoadingData"
          >
            <template v-slot:cell(name)="data">
              <a
                style="font-size: 15px"
                :href="buildRoute(data.item)"
                variant="link"
              >{{data.item.name}}</a>
            </template>
            <template v-slot:cell(dateCreated)="data">{{data.item.dateCreated.toDate() | date}}</template>
            <template v-slot:cell(quantity)="data">{{sumQuantity(data.item)}}</template>
            <template v-slot:cell(total)="data">{{sumTotal(data.item) | currency(vndConfig)}}</template>
            <template v-slot:cell(action)="data">
              <b-button
                type="submit"
                size="sm"
                variant="danger"
                @click="deleteOrder(data.item)"
                v-if="sumQuantity(data.item) == 0"
              >
                <i class="fa fa-remove"></i> Delete
              </b-button>
            </template>
          </b-table>
          <b-row class="load-more">
            <b-button
              variant="outline-success"
              @click="loadMore($event)"
              v-loading="isLoadingData"
            >Load more</b-button>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
    <router-view></router-view>
    <!--/.row-->
  </div>
</template>

<script>
import fb from "firebase";
import { mapState, mapActions } from "vuex";

export default {
  name: "Orders",
  components: {},
  data() {
    return {
      orders: [],
      selectedOrder: {},
      isLoadingData: false,
      lastVisible: undefined,
      fields: [
        { key: "name" },
        { key: "dateCreated", label: "Created Date" },
        { key: "quantity", label: "Quantity" },
        { key: "total", label: "Total" },
        { key: "action", class: "text-right" }
      ]
    };
  },
  mounted: function() {
    this.loadOrders();
  },
  computed: {
    ...mapState("counter", {
      orderCount: state => state.counter && state.counter.orderCount
    })
  },
  methods: {
    ...mapActions("counter", ["increaseOrderCount", "decreaseOrderCount"]),
    ...mapActions("order", ["getAllOrders", "addOrUpdateOrder"]),
    loadOrders: async function() {
      this.isLoadingData = true;
      this.orders = _.orderBy(await this.getAllOrders(), ["dateCreated"], ["desc"]);
      this.isLoadingData = false;
    },
    addNewOrder: async function() {
      var order = {
        name: this.buildOrderName(),
        dateCreated: new Date(),
        status: "OPEN"
      };
      order = await this.addOrUpdateOrder(order);
      await this.increaseOrderCount();
      this.loadOrders();

      // var newOrder = this.$fb.orderCollection.doc();
      // order.id = newOrder.id;
      // newOrder.set(order).then(() => {
      //   this.$fb.orderCollection
      //     .doc(order.id)
      //     .get()
      //     .then(doc => {
      //       this.orders.unshift(doc.data());
      //       //update order counter
      //       this.increaseOrderCount();
      //     });
      // });
    },
    buildOrderName: function() {
      var orderCount = (this.orderCount || 0) + 1;
      if ((orderCount + "").endsWith("1")) return orderCount + "st Order";
      if ((orderCount + "").endsWith("2")) return orderCount + "nd Order";
      if ((orderCount + "").endsWith("3")) return orderCount + "rd Order";
      return orderCount + "th Order";
    },
    orderClicked: function(ord) {
      this.selectedOrder = ord;
      this.$router.push({ name: "OrderStatistic", params: { id: ord.id } });
    },
    deleteOrder: function(ord) {
      var self = this;
      this.$fb.orderCollection
        .doc(ord.id)
        .delete()
        .then(() => {
          var index = self.orders.findIndex(x => x.id == ord.id);
          if (index > -1) self.orders.splice(index, 1);

          this.decreaseOrderCount();
        });
    },
    sumQuantity: function(order) {
      if (!order.items || order.items.length == 0) return 0;
      return order.items.reduce((sum, x) => (sum += x.quantity), 0);
    },
    sumTotal: function(order) {
      if (!order.items || order.items.length == 0) return 0;
      return order.items.reduce(
        (sum, x) => (sum += x.quantity * parseFloat(x.rndPrice || x.product.rndPrice)),
        0
      );
    },
    buildRoute: function(item) {
      var { origin } = location;
      return this.$router.resolve({
        name: "OrderDetail",
        params: { id: item.id }
      }).href;
    },
    loadMore: function(e) {
      this.loadOrders();
    }
  }
};
</script>
