import fb from "@/shared/firebaseConfig"

const state = {
    orders: [],
    lastVisible: undefined
}

const getters = {

}
const actions = {
    getAllOrders: function ({ commit, state }) {
        return new Promise(function (resolve, reject) {
            if (state.orders.length > 0) {
                console.log("Get all orders from store");
                resolve(state.orders);
            }
            else {
                console.log("Get all orders");
                fb.orderCollection.onSnapshot(ss => {
                    console.log("update order to store (listen)");
                    if (ss.empty) {
                        commit("setOrders", []);
                        commit("setLastVisible", undefined);
                        resolve([]);
                    }
                    else {
                        var orders = ss.docs.map(x => x.data());
                        var lastVisible = ss.docs[ss.docs.length - 1];
                        commit("setOrders", orders);
                        commit("setLastVisible", lastVisible);
                        resolve(orders);
                    }
                });
            }
        });
    },
    getOrderById: async function ({ dispatch }, id) {
        return (await dispatch("getAllOrders")).find(x => x.id == id);
    },
    addOrUpdateOrder: async function ({ dispatch, commit }, order) {
        if (order.id) {
            order.dateModified = new Date();
            await fb.orderCollection.doc(order.id).set(order);
            //commit("updateOrder", order);
        }
        else {
            order.dateCreated = new Date();
            var ref = fb.orderCollection.doc();
            order.id = ref.id;
            await ref.set(order);
            //commit("addOrder", order);
        }

        return order;
    },
    deleteOrder: async function () {

    }
};
const mutations = {
    setOrders: function (state, orders) {
        state.orders = orders;
    },
    addOrder: function (state, order) {
        state.orders.push(order);
    },
    updateOrder: function (state, order) {
        var index = state.orders.findIndex(x => x.id = order.id);
        state.orders.splice(index, 1, order);
    },
    setLastVisible: function (state, lastVisible) {
        state.lastVisible = lastVisible;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}