import fb from "@/shared/firebaseConfig"

const state = {
    counter: undefined
}

const getters = {};

const actions = {
    getCounter: async function ({ commit, state }) {
        return new Promise(function (resolve, reject) {
            if (state.counter) {
                resolve(state.counter);
            }
            else {
                console.log("Get app counter");
                fb.counterCollection.limit(1).get().then(ss => {
                    if (ss.empty) resolve(undefined);
                    else {
                        var counter = ss.docs[0].data();
                        commit("setCounter", counter);
                        resolve(counter);
                    }
                })
            }
        });
    },
    setCounter: async function ({ commit }, counter) {
        fb.counterCollection.doc(counter.id).set(counter).then(() => {
            commit("setCounter", counter);
        });
    },
    increaseCustomerCount: async function ({ dispatch }) {
        let counter = await dispatch("getCounter");
        counter.customerCount++;
        dispatch("setCounter", counter);
    },
    decreaseCustomerCount: async function ({ dispatch }) {
        let counter = await dispatch("getCounter");
        counter.customerCount--;
        dispatch("setCounter", counter);
    },
    getCustomerCount: async function ({ dispatch }) {
        return await dispatch("getCounter").then(counter => counter.customerCount);
    },
    increaseProductCount: async function ({ dispatch }) {
        let counter = await dispatch("getCounter");
        counter.productCount++;
        dispatch("setCounter", counter);
    },
    decreaseProductCount: async function ({ dispatch }) {
        let counter = await dispatch("getCounter");
        counter.productCount--;
        dispatch("setCounter", counter);
    },
    getProductCount: async function ({ dispatch }) {
        return await dispatch("getCounter").then(counter => counter.productCount);
    },
    increaseOrderCount: async function ({ dispatch }) {
        let counter = await dispatch("getCounter");
        counter.orderCount++;
        dispatch("setCounter", counter);
    },
    decreaseOrderCount: async function ({ dispatch }) {
        let counter = await dispatch("getCounter");
        counter.orderCount--;
        dispatch("setCounter", counter);
    },
    getOrderCount: async function ({ dispatch }) {
        return await dispatch("getCounter").then(counter => counter.orderCount);
    }
};

const mutations = {
    setCounter: function (state, counter) {
        state.counter = counter;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}

