// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'core-js/es6/promise'
import 'core-js/es6/string'
import 'core-js/es7/array'
// import cssVars from 'css-vars-ponyfill'
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
import App from './App'
import router from './router'
import './shared/filters'
import './shared/directives'
import axios from "axios"
import fb from "@/shared/firebaseConfig"
import fbServices from "@/shared/firebaseServices"
import VueCurrencyFilter from 'vue-currency-filter'
import store from "@/store/index";
import { mapState, mapActions } from "vuex"

window.$ = require('jquery')


// global variables

Vue.prototype.$mapState = mapState;
Vue.prototype.$mapActions = mapActions;
Vue.prototype.$fb = fb;
Vue.prototype.$fbServices = fbServices;
Vue.prototype.vndConfig = {
  symbol: 'VND',
  thousandsSeparator: ',',
  fractionCount: 0,
  fractionSeparator: '.',
  symbolPosition: 'after',
  symbolSpacing: true
};

Vue.prototype.audConfig = {
  symbol: '$',
  thousandsSeparator: ',',
  fractionCount: 2,
  fractionSeparator: '.',
  symbolPosition: 'front',
  symbolSpacing: false
};
Vue.prototype.$http = axios;


Vue.use(BootstrapVue)
Vue.use(VueCurrencyFilter);

Vue.prototype.showMessage = function (mes, title) {
  this.$bvToast.toast(mes, { title: title || "Notification", variant: "info", autoHideDelay: 2000 });
}

Vue.prototype.showError = function (mes, title) {
  this.$bvToast.toast(mes, { title: title || "Error", variant: "danger", autoHideDelay: 2000 });
}


let app = '';

//load settings from FB
fb.auth.onAuthStateChanged(async () => {
  if (fb.auth.currentUser) {
    console.log("Get app settings");
    await fbServices.settingService.getSettings().then(settings => {
      Vue.prototype.appSettings = settings;
      window.appSettings = settings;
    });
    await store.dispatch("counter/getCounter");
  }
  if (!app) {

    /* eslint-disable no-new */
    app = new Vue({
      el: '#app',
      router,
      store,
      template: '<App/>',
      components: {
        App
      }
    });
  }

});



