<template>
  <b-row v-if="enable">
    <b-col sm="12">
      <div class="alert alert-primary" role="alert">
        <template v-if="isChecking">
          <div class="spinner-border spinner-border-sm text-success" role="status">
            <span class="sr-only"></span>
          </div>&nbsp;&nbsp;Checking Pandora Price...
        </template>
        <template v-if="!isChecking">
          <template v-if="matchedProducts.length == 0">Pandora: No results</template>
          <template v-else>
            <div>
              <p>Pandora (PID: #{{this.product.paProductId}}): {{matchedProducts.length}} item(s) found on {{lastChecked | date}}</p>
              <table class="table table-borderless">
                <tr v-for="(item, index) in matchedProducts" :key="index">
                  <view-port>
                    <template v-slot:desktop>
                      <td style="width: 20%">#{{item.id}}</td>
                      <td>{{item.label}}</td>
                      <td style="width: 20%">
                        <small v-if="item.oldPrice != 0">
                          <del>{{item.oldPrice | currency(audConfig)}}</del>
                        </small>
                        {{item.price | currency(audConfig)}}
                        <i
                          class="fa fa-angle-double-up fa-lg"
                          aria-hidden="true"
                          v-if="item.priceStatus == 'increase'"
                          style="color: red"
                        ></i>
                        <i
                          class="fa fa-angle-double-down fa-lg"
                          aria-hidden="true"
                          v-if="item.priceStatus == 'decrease'"
                          style="color: green"
                        ></i>
                        <span v-if="item.percentage > 0">({{item.percentage}}%)</span>
                      </td>
                      <td class="text-right" style="width: 10%">
                        <b-button
                          type="submit"
                          size="sm"
                          variant="success"
                          @click="matchProduct(item)"
                          :disabled="selected.id == item.id"
                        >
                          <i class="fa fa-check"></i> Select
                        </b-button>
                      </td>
                    </template>
                    <template v-slot:mobile>
                      <td style="width: 100%">
                        {{index + 1}}. {{item.label}} (#{{item.id}})
                        -
                        <small>
                          <del>{{item.oldPrice | currency(audConfig)}}</del>
                        </small>
                        {{item.price | currency(audConfig)}}
                        <i
                          class="fa fa-angle-double-up fa-lg"
                          aria-hidden="true"
                          v-if="item.priceStatus == 'increase'"
                          style="color: red"
                        ></i>
                        <i
                          class="fa fa-angle-double-down fa-lg"
                          aria-hidden="true"
                          v-if="item.priceStatus == 'decrease'"
                          style="color: green"
                        ></i>
                        ({{item.percentage}}%)
                        <br />
                        <b-button
                          type="submit"
                          size="sm"
                          variant="success"
                          @click="matchProduct(item)"
                          :disabled="selected.id == item.id"
                        >
                          <i class="fa fa-check"></i> Select
                        </b-button>
                      </td>
                    </template>
                  </view-port>
                </tr>
              </table>
            </div>
          </template>
        </template>
      </div>
    </b-col>
  </b-row>
</template>

<script>
import viewPort from "../shared/viewPort";
const convert = require("html-to-json-data");
const {
  group,
  text,
  number,
  href,
  src,
  uniq,
  attr,
  data
} = require("html-to-json-data/definitions");

export default {
  name: "PAProductPredictive",
  components: { viewPort },
  props: [],
  data() {
    return {
      matchedProducts: [],
      isChecking: false,
      product: {},
      lastChecked: undefined,
      selected: {}
    };
  },
  mounted: function() {},
  watch: {},
  computed: {
    enable: function() {
      return this.appSettings.enablePAProductPredictive;
    }
  },
  methods: {
    checkPrice: function(product, forceCheckAgain) {
      if (!this.enable) return;
      this.product = product;
      this.selected = {};
      this.matchedProducts = [];
      if (!this.product || !this.product.name) return;
      var today = new Date().setMin();

      var { name, paTracking, paProductId } = this.product;
      if (
        !forceCheckAgain &&
        paTracking &&
        paTracking.lastChecked &&
        paTracking.lastChecked.toDate() > today
      ) {
        this.matchedProducts.push({
          supplier: "PA",
          id: paProductId,
          priceStatus: paTracking.priceStatus,
          price: paTracking.price,
          oldPrice: paTracking.oldPrice,
          percentage: paTracking.percentage,
          label: this.product.name
        });
        this.lastChecked = paTracking.lastChecked.toDate();
        return;
      }

      var term = paProductId || name;
      this.isChecking = true;

      this.callPACheckPriceApi(term).then(res => {
        if (res && res.status == 200 && res.data) {
          var products = this.parseHtml2Json(res.data);
          if (products && products.length > 0) {
            var decoratedProducts = _.map(products, this.decorateData);
            decoratedProducts.forEach(item => this.matchedProducts.push(item));
          }
        }
        this.lastChecked = new Date();
        this.isChecking = false;
      });
    },
    parseHtml2Json: function(html) {
      var result = convert(html, {
        paProducts: group(".Search-suggestion__product", {
          url: data(":self", "url"),
          imgSrc: attr("img", "src"),
          label: text(".search-suggestion-right-panel-product-name"),
          priceRange: attr(".pricerange input[type=hidden]", "value"),
          prices: attr(".product-price input[class*=price]", "value"),
          saleprice: attr(".product-price input[class*=saleprice]", "value"),
          stdprice: attr(".product-price input[class*=stdprice]", "value")
        })
      });
      console.log(result);
      return result.paProducts;
    },
    callPACheckPriceApi: async function(term) {
      var url = this.appSettings.paProductGetSuggestion.replace("{q}", term);
      var res = await this.$http.get(url);
      return res;
    },
    decorateData: function(item) {
      item.id = item.url && item.url.substring(item.url.lastIndexOf("/") + 1).replace(".html", "");
      item.price = parseFloat(item.saleprice);
      item.thumbnail = item.imgSrc;

      var price = item.price;
      if (!this.product.supplierPrices || this.product.supplierPrices.length == 0) {
        this.product.supplierPrices = [];
        this.product.supplierPrices.push({
          supplier: "PA"
        });
      }
      var supplierPrice = this.product.supplierPrices.find(x => x.supplier == "PA");
      var oldPrice = parseFloat((supplierPrice && supplierPrice.price) || 0);
      item.supplier = "PA";
      item.oldPrice = oldPrice;
      if (oldPrice == 0) {
        item.priceStatus = "same";
        item.percentage = 0;
      } else if (price > oldPrice + 0.5) {
        item.priceStatus = "increase";
        item.percentage = (((price - oldPrice) / oldPrice) * 100).toFixed(0);
      } else if (price < oldPrice - 0.5) {
        item.priceStatus = "decrease";
        item.percentage = (((oldPrice - price) / oldPrice) * 100).toFixed(0);
      } else {
        item.priceStatus = "same";
        item.percentage = 0;
      }

      return item;
    },
    matchProduct: function(item) {
      this.selected = item;
      this.$emit("productMatched", { ...item });
    }
  }
};
</script>
